import React, {Component} from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import OraModal from "../../core/components/ui/Modal";
import {HideWhenCartIsEmpty} from "./Utils";
import AddComponent from "./AddComponent";
import {getKdrNumber, getKdrSource, getKdrAccepted, getKdrErrors, getKdrSaving} from "../selectors";
import {changeKdrNumber, getKdrDataFromCart, clearKdrNumber, saveKdrData, approveKdrNumber, dispatchKdrError} from "../actions/cart";
import SingleInputModalContent from "./SingleInputModalContent";
import {kdrNumberPattern} from "../constants";
import {OraLoader} from "eshop/components/OraCommonComponents";

export class AddKdrComponent extends Component {
    constructor(props) {
        super(props);
        this.state = ({showModal: false});
    }

    componentDidMount() {
        this.props.getKdrNumberFromCart();
    }

    componentDidUpdate(prevProps) {
        if(prevProps.saving && !this.props.saving && (!this.props.errors || Object.keys(this.props.errors).length === 0)) {
            this.closeModal();
        }
    }

    setShowModal(modalState) {
        this.setState({showModal: modalState});
    }

    onAddClick() {
        this.setShowModal(true);
    }

    getModalProps() {
        return {
            open: this.state.showModal,
            size: "small",
            showClose: true,
            escapeClose: false,
            clickClose: false,
            onClose: this.closeModal.bind(this)
        };
    }

    clearIfNotAccepted() {
        if(!this.props.kdrAccepted) {
            this.props.changeKdrNumber("");
        }
    }

    closeModal() {
        this.clearIfNotAccepted();
        this.setShowModal(false);
    }

    getModalContent() {
        return <SingleInputModalContent {...this.getModalContentProps()}/>;
    }

    getAddedRovProps() {
        return {
            showAddLink: false,
            addBtnText: getDescription('addBtnText', this.props),
            label: getDescription('addedRowTitle', this.props),
            tooltipBtnText: getDescription('tooltipBtnText', this.props),
            tooltipContent: getDescription('addedRowTooltip', this.props),
            tooltipId: "addedKdr",
            addRowIcon: getDescription('addedRowIcon', this.props),
            descriptions: {}
        };
    }

    getAddedRowView() {
        return (
            <AddComponent {...this.getAddedRovProps()}>
                <p className="u-font-normal">{this.props.kdrNumber}</p>
                {
                    this.props.kdrSource === "legacy" ?
                        null :
                        <a href="#" className="u-font-bold" onClick={this.clearData.bind(this)}>
                            Usu≈Ñ
                        </a>
                }
            </AddComponent>
        );
    }

    getModalContentProps() {
        return {
            id: "kdrNumber",
            inputValue: this.props.kdrNumber,
            error: this.props.errors,
            mask: "99999999999999999",
            placeholder: "_________________",
            modalHeader: getDescription('modalHeader', this.props),
            modalMainText: getDescription('modalMainText', this.props),
            inputPlaceHolder: getDescription('modalInputPlaceholder', this.props),
            modalBtnText: getDescription('modalBtnText', this.props),
            onButtonClick: this.onInputNext.bind(this),
            setInputValue: this.changeKdrNumber.bind(this)
        };
    }

    onInputNext(value) {
        if(!kdrNumberPattern.test(this.props.kdrNumber)) {
            this.props.dispatchKdrError({level: "error", message: getDescription('kdrNumberError', this.props)});
        } else {
            this.props.approveKdrNumber();
            this.props.saveKdrData({
                number: this.props.kdrNumber,
                source: this.props.kdrSource
            });
        }
    }

    clearData(event) {
        event.preventDefault();
        this.props.clearKdrNumber();
    }

    changeKdrNumber({id, name, value}) {
        this.props.changeKdrNumber(value);
    }

    render() {
        return (
            <HideWhenCartIsEmpty className="g-gray1-bg">
                <div className="l-page-row u-padding-l u-small-padding-left u-small-padding-right">
                    <OraModal {...this.getModalProps()} id="kdrNumberModal">
                        <OraLoader id="kdrLoader"
                                   loading={this.props.saving}
                                   parentId="kdr-loader">
                            {this.getModalContent()}
                        </OraLoader>
                    </OraModal>

                    {
                        this.props.kdrAccepted ?
                            this.getAddedRowView() :
                            <AddComponent {...this.props} onAddClick={this.onAddClick.bind(this)}/>
                    }
                </div>
            </HideWhenCartIsEmpty>
        )
    }
}

const mapStateToProps = state => ({
    kdrNumber: getKdrNumber(state),
    kdrSource: getKdrSource(state),
    errors: getKdrErrors(state),
    saving: getKdrSaving(state),
    kdrAccepted: getKdrAccepted(state)
});

const mapDispatchToProps = (dispatch) => ({
    changeKdrNumber: (kdrNumber) => dispatch(changeKdrNumber(kdrNumber)),
    getKdrNumberFromCart: () => dispatch(getKdrDataFromCart()),
    approveKdrNumber: () => dispatch(approveKdrNumber()),
    clearKdrNumber: () => dispatch(clearKdrNumber()),
    saveKdrData: (kdrData) => dispatch(saveKdrData(kdrData)),
    dispatchKdrError: (error) => dispatch(dispatchKdrError(error))
});

AddKdrComponent.propTypes = {
    label: PropTypes.string,
    addBtnText: PropTypes.string,
    tooltipBtnText: PropTypes.string,
    tooltipId: PropTypes.string.isRequired,
    modalHeader: PropTypes.string,
    modalMainText: PropTypes.string,
    modalBtnText: PropTypes.string,
    modalInputPlaceholder: PropTypes.string,
    addRowIcon: PropTypes.string,
    addedRowTitle: PropTypes.string,
    addedRowIcon: PropTypes.string,
    addedRowTooltip: PropTypes.string,
    changeKdrNumber: PropTypes.func,
    getKdrNumberFromCart: PropTypes.func,
    approveKdrNumber: PropTypes.func,
    clearKdrNumber: PropTypes.func,
    kdrNumber: PropTypes.string,
    kdrSource: PropTypes.string,
    kdrAccepted: PropTypes.bool
};

AddKdrComponent.defaultProps = {
    label: "Posiadam Kartƒô Du≈ºej rodzinki",
    addBtnText: "Kliknij aby potwierdziƒá posiadanie Karty Du≈ºej rodzinki",
    tooltipBtnText: "Otw√≥rz podpowied≈∫",
    tooltipId: "tooltip_kdr",
    modalHeader: "Karta Du≈ºej Rodzinki",
    modalMainText: "",
    modalBtnText: "Potwierd≈∫",
    modalInputPlaceholder: "Wpisz nr karty Du≈ºej rodzinki",
    addRowIcon: "g-icon--voucher",
    addedRowTitle: "Karta Du≈ºej Rodziny",
    addedRowIcon: "g-icon--person-group2",
    addedRowTooltip: "",
    kdrNumberError: "Nieprawid≈Çowy numer Karty Du≈ºej rodzinki"
};

function getDescription(descriptionName, props) {
    return props.descriptions[descriptionName] || props[descriptionName];
}

export default connect(mapStateToProps, mapDispatchToProps)(AddKdrComponent);